*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Work Sans', sans-serif;
}

.app{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

header h1{
    margin: 50px 0 30px 0px;
    text-align: center;
    font-size: 40px;
}

header p{
    font-size: 20px;
}

.pads{

    width: 100%;
    display: flex;
}

.pads > div{
    height: 100px;
    width: 100px;
    flex: 1;
    border-radius: 10px;
    cursor: pointer;
}

.pad1{
    background: #60d394;
}

.pad2{
    background: #d36060;
}

.pad3{
    background: #c060d3;
}

.pad4{
    background: #d3d160;
}

.pad5{
    background: #6860d3;
}

.pad6{
    background: #60b2d3;
}

@keyframes jump{
    0%{
        bottom: 0%;
        left: 20%;
    }

    50%{
        bottom: 50%;
        left: 50%;
    }

    100%{
        bottom: 0%;
        left: 80%;
    }
}

.visual > div {
    position: absolute;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    bottom: 0%;
    left: 20%;
    z-index: -1;
}

.add{
    opacity: .9;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}